import { Component, $$, Button, Icon, StackFill, HorizontalStack, Limiter, Link } from 'substance'
import LoginStatus from './LoginStatus'
import SubstanceLogo from './SubstanceLogo'

export default class DashboardHeader extends Component {
  render () {
    const { user, profile } = this.props
    return $$('div', { class: 'sc-header sc-dashboard-header' },
      $$(Limiter, {},
        $$(HorizontalStack, {},
          $$(Link, { href: '/' + profile.name },
            $$(SubstanceLogo)
          ),
          $$(StackFill),
          $$(Button, { style: 'plain', size: 'default', url: '/new' },
            $$(Icon, { icon: 'plus' })
          ),
          $$(LoginStatus, { user })
        )
      )
    )
  }
}
